<template>
  <div class="wrap">
    <van-nav-bar
      title="业务介绍"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />
    <businessDetail></businessDetail>
  </div>
</template>

<script>
  import businessDetail from '../../../components/businessDetail'
    export default {
        name: "businessIntroduction",
      components : {
        businessDetail
      },
      data(){
        return {

        }
      },
      methods:{
        onClickLeft(){
          this.$router.back();
        }
      }
    }
</script>

<style scoped>
.wrap {
  height: 100%;
}
</style>



